body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: table;
  width: 100%;
}

.runtime-status-failed {
    background-color: rgb(255, 234, 228);
}

.runtime-status-completed {
    background-color: rgb(242, 250, 240);
}

.runtime-status-running {
    background-color: rgb(252, 250, 236);
}

.runtime-status-listening {
    background-color: rgb(236, 244, 252);
}

.runtime-status-terminated {
    background-color: rgb(235, 235, 235)
}

.top-toolbar {
    padding-top: 5px;
    padding-bottom: 15px;
    max-width: 100vw;
    min-width: 900px;
}

.toolbar-select {
    min-width: 150px
}

.long-text-cell {
    min-width: 200px;
    max-width: 250px;
}

.long-text-cell-input {
    font-size: x-small !important;
}

.empty-table-placeholder {
    padding: 30px;
    text-align: center;
}

.name-cell {
    min-width: 180px;
    word-break: break-all;
}

.refresh-button {
    width: 150px;
}

.title-typography {
    padding-right: 10px;
}

.app-bar {
    margin-bottom: 10px;
}

.login-progress {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

.message-snackbar {
    top: 80px !important;
}

.message-icon {
    margin-right: 10px;
}

.error-snackbar-content {
    background-color: red !important;
}


.datetime-cell {
    min-width: 190px;
}

.till-checkbox {
    padding-top: 20px !important;
    padding-bottom: 4px !important;
}

.till-label {
    padding-left: 15px !important;
}

.from-input {
    width: 247px;
}

.till-input {
    width: 215px;
}

.filter-value-input {
    width: 350px;
}

.filtered-column-input {
    width: 180px;
}

.items-count-label {
    padding-left: 15px;
}

.instance-id-cell {
    min-width: 180px;
    max-width: 270px;
    word-break: break-all;
}

.output-cell {
    min-width: 100px;
    max-width: 200px;
}
.grid-container {
    padding-top: 20px;
    padding-right: 15px;
}

.grid-item {
    padding-left: 15px;
}

.details-datetime-cell {
    min-width: 250px;
}

.history-events-count-label {
    padding-top: 10px;
    padding-left: 16px;
    padding-bottom: 10px;
}

.purge-history-from-input {
    
}

.purge-history-till-input {
    margin-left: 20px !important;
}

.purge-history-statuses {
    margin-top: 20px !important;
}

.success-message {
    color: green !important;
}
